@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
* {
    margin: 0;
    padding: 0;
}

body {
    background-color: #fff;
    font-family: 'Montserrat', sans-serif;
    overflow-x: hidden;
}

.navbar {
    background-color: #ffffff;
    justify-content: center;
}

.app_module {
    background-color: #ffffff;
    border-radius: 5px;
    justify-content: space-evenly;
}

  
*{
    margin-right: 0;
    padding: 0;
}

.landing-container {
    height: 100vh;
    width: 100vw;
    position: relative;
    background-color: #fff;
    overflow-x: hidden;
  }
  
  .landing-header {
    position: absolute;
    top: 0;
    right: 0; /* replace 100px with the actual width of your header element */
  }
  

  .landing-header {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
  }
  
  .landing-logo-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 100px;
    background-color: #fff;
    border-radius: 50%;
    margin-right: 20px;
  }
  
  .landing-logo {
    margin-right: 10px;
    height:100px;
    width: 100px;
  }
  
  .landing-title {
    display: inline-block;
  }
  
  .landing-content {
    height: calc(100% - 50px);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .landing-form {
    width: 100%;
    max-width: 500px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .landing-form:hover {
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
  }

  .landing-form-header {
    margin-bottom: 20px;
    text-align: center;
  }
  
  .landing-form-toggle {
    margin-top: 20px;
    text-align: center;
  }
  
  .landing-footer {
    text-align: center;
  }
  

  
/* Profile container */
.profile-container {
    margin-top: 20px;
  }
  
  /* Profile header */
  .profile-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .profile-avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #ccc;
    margin-right: 20px;
  }
  
  .profile-info {
    flex: 1 1;
  }
  
  .profile-username {
    font-size: 24px;
    font-weight: bold;
  }
  
  .profile-bio {
    font-size: 16px;
  }
  
  /* Profile actions */
  .profile-actions {
    margin-top: 20px;
  }
  
  .profile-actions button {
    margin-right: 10px;
  }
  
  /* Post list */
  .post-list {
    margin-top: 20px;
  }
  
/* ChatBox.css */
.chat-box {
    background-color: #f5f5f5;
    border-radius: 8px;
    padding: 16px;
    overflow-y: auto;
    height: 300px;
  }
  
  .chat-box .message {
    margin-bottom: 12px;
  }
  
  .chat-box .message:last-child {
    margin-bottom: 0;
  }
  
  .chat-box .message .user {
    font-weight: bold;
  }
  
  .chat-box .message .time {
    color: #888;
    font-size: 0.8em;
  }
  
  /* ChatInput.css */
  .chat-input {
    display: flex;
    align-items: center;
    margin-top: 16px;
  }
  
  .chat-input input[type="text"] {
    flex-grow: 1;
    padding: 8px;
    border: none;
    border-radius: 4px;
    outline: none;
    font-size: 1em;
  }
  
  .chat-input button {
    margin-left: 8px;
    padding: 8px 12px;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
    outline: none;
    font-size: 1em;
  }
  
  .chat-input button:hover {
    background-color: #0056b3;
  }
  
  /* ChatInvite.css */
  .chat-invite {
    margin-top: 16px;
  }
  
  .chat-invite input[type="text"] {
    width: 100%;
    padding: 8px;
    border: none;
    border-radius: 4px;
    outline: none;
    font-size: 1em;
  }
  
  .chat-invite button {
    margin-top: 8px;
    padding: 8px 12px;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
    outline: none;
    font-size: 1em;
  }
  
  .chat-invite button:hover {
    background-color: #0056b3;
  }
  
